<template>
  <div id="comingSoon">
    <section>
      <div style="padding:0.4rem">正在拼命赶制该功能中，敬请期待....</div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      setTimeout(() => {
        vm.$router.go(-1);
      }, 2000);
    });
  }
};
</script>

<style lang="scss">
#comingSoon {
  background-color: #151a1d;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
  section {
    background-color: #fff;
    width: 4rem;
    height: 1.4rem;
    color: #4b4f59;
    font-size: 0.28rem;
    border-radius: 0.1rem;
  }
}
</style>



